.ml-swipe {
    width: 100%;
    height: 150px;
    overflow: hidden;
    position: relative;
}
.ml-swipe-sele {
    background: #ff976a !important;
}
.ml-swipe-main {
    /* overflow: hidden; */
    position: relative;
    height: 100%;
}
.ml-swipe-border {
    position: fixed;
    top: 125px;
    width: 50%;
    text-align: center;
    left: 25%;
}
.ml-swipe-border span {
    width: 8px;
    margin-left: 4px;
    height: 8px;
    display: inline-block;
    background: rgba(87, 86, 86, 0.3);
    border-radius: 50%;
}
.ml-swipe-border span:nth-child(1) {
    margin: 0;
}

.ml-swipe-item{
    background: red;
    height: 100%;
    float: left;
}
.ml-swipe-item img{
    width: 100vw;
    height: 150px;
}

.ml-alert {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99999;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, .5);
}
.ml-alert-main {
    width: 85%;
    background: white;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate3d(-50%, -50%, 0);
    transform: translate3d(-50%, -50%, 0);
    text-align: center;
    border-radius: 3px;
}
.ml-alert-cenal {
    color: #333;
    border-right: 1px solid #eee !important;
}
.ml-alert-confirm {
    color: #26a2ff;
}
.ml-alert-content {
    padding: 10px 20px 15px;
    border-bottom: 1px solid #ddd;
    min-height: 36px;
    position: relative;
    color: #999;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.ml-alert-title {
    padding-top: 16px;
    font-size: 16px;
    font-weight: 700;
}
.ml-alert-button {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.ml-alert-button button {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    background: none;
    border: 0;
    height: 35px;
}

.ml-toast {
    padding: 7px 8px 8px 8px;
    color: white;
    display: inline-block;
    background: rgba(0, 0, 0, .6);
    position: fixed;
    font-size: 12px;
    border-radius: 5px;
    text-align: center;
    left: 50%;
    -webkit-transform: translate3d(-50%, -50%, 0);
    transform: translate3d(-50%, -50%, 0);
}
.ml-toast-middle {
    top: 50%;
}
.ml-toast-bottom {
    top: 90%;
}
.ml-toast-top {
    top: 8%;
}

